<script>
  import data from "./fixtures.json";

  export let projectIndex;
  let currentIndex = projectIndex;

  $: name = data.projects[currentIndex].name;
  $: type = data.projects[currentIndex].type;
  $: summary = data.projects[currentIndex].summary;
  $: deliverables = data.projects[currentIndex].deliverables;
  $: link = data.projects[currentIndex].link;
  $: images = data.projects[currentIndex].images;
  $: previousProject = data.projects[currentIndex - 1];
  $: nextProject = data.projects[currentIndex + 1];
  $: timeframe = data.projects[currentIndex].timeframe;

  const goToPrevProject = () => {
    currentIndex -= 1;
  };

  const goToNextProject = () => {
    currentIndex += 1;
  };

  let projectNavigationHeight;
  let projectDetailWidth;

  function handleKeydown(event) {
    if (event.code === "ArrowRight" && nextProject) {
      goToNextProject();
    }

    if (event.code === "ArrowLeft" && previousProject) {
      goToPrevProject();
    }
  }
</script>

<style>
  .remove-scrollbar::-webkit-scrollbar {
    display: none;
  }

  .remove-scrollbar {
    -ms-overflow-style: none;
  }
</style>

<svelte:window on:keydown={handleKeydown} />

<section
  class="relative lg:static max-h-screen overflow-y-auto lg:overflow-y-hidden
  px-4 lg:px-20 pt-16 lg:pt-24">
  <section
    class="flex flex-col-reverse lg:flex-row"
    style="padding-bottom: {projectNavigationHeight}px">

    <div
      class="flex flex-col mr-0 lg:mr-20 static lg:relative lg:max-h-screen
      overflow-y-scroll remove-scrollbar pb-6 lg:pb-32"
      style="flex: 4">
      {#each images as image, index}
        <div class="mb-10">
          <div class="flex justify-between">
            <span class="text-sm font-body text-white">{image.title}</span>
            <span class="text-sm font-body text-white opacity-50 text-right">
              {index + 1} of {images.length}
            </span>
          </div>

          <hr class="border-white border-t-2 opacity-25 my-4" />

          <img
            class="w-full"
            srcset={`
          images/projects/${name.replace(/ /g, '%20')}/${image.title.replace(/ /g, '%20')}@2x.jpg 2x`}
            src="images/projects/{name}/{image.title}.jpg"
            alt={image.title} />
        </div>
      {/each}
    </div>

    <div class="mb-16" style="flex: 3" bind:clientWidth={projectDetailWidth}>
      <h3 class="mb-4 text-sm text-white font-body uppercase tracking-widest">
        {type}
      </h3>

      <h2 class="mb-10 text-4xl text-white font-display">{name}</h2>

      <div class="mb-8">
        <h4 class="mb-2 tex-sm text-white font-body opacity-50 uppercase">
          Timeframe
        </h4>
        <p class="text-base text-white font-body">{timeframe}</p>
      </div>

      <div class="mb-8">
        <h4 class="mb-2 tex-sm text-white font-body opacity-50 uppercase">
          Summary
        </h4>
        <p class=" text-base text-white font-body">{summary}</p>
      </div>

      <div class="mb-10">
        <h4 class="mb-2 tex-sm text-white font-body opacity-50 uppercase">
          Deliverables
        </h4>
        <p class=" text-base text-white font-body">{deliverables.join(', ')}</p>
      </div>

      <a
        href={link}
        class="border-none text-base text-white font-body underline">
        Link to the project ↗
      </a>

    </div>

  </section>

  <div
    bind:clientHeight={projectNavigationHeight}
    class="bg-teal base-shadow border-white border-2 flex mt-16 fixed bottom-0
    inset-x-0 lg:right-0 lg:left-auto mx-4 mb-6 lg:mr-24"
    style="width: {projectDetailWidth}px">

    {#if previousProject}
      <button
        on:click={goToPrevProject}
        class="flex flex-col items-end p-6 flex-1 border-none">
        <span
          class="text-sm font-body text-white text-right uppercase opacity-50
          mb-2">
          ← Prev
        </span>
        <span
          class="text-xl font-body text-white text-right truncate ..."
          style="max-width: 7.5rem">
          {previousProject.name}
        </span>
      </button>
    {/if}

    {#if previousProject && nextProject}
      <div class="flex bg-white opacity-25" style="width: 2px" />
    {/if}

    {#if nextProject}
      <button
        on:click={goToNextProject}
        class="flex flex-col items-start p-6 flex-1 border-none">
        <span
          class="text-sm font-body text-white text-left uppercase opacity-50
          mb-2">
          Next →
        </span>
        <span
          class="text-xl font-body text-white text-left truncate ..."
          style="max-width: 7.5rem">
          {nextProject.name}
        </span>
      </button>
    {/if}

  </div>
</section>
